@use '../base/mixins' as mixin;

.contacs-section {
  .mb-lg-56 {
    margin-bottom: 56px;
  }

  .mb-sm-40 {
    @include mixin.small-laptop {
      margin-bottom: 40px;
    }
  }

  .mb-xs-16 {
    @include mixin.phone {
      margin-bottom: 16px;
    }
  }
}

.contact {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 24px;

  &__icon {
    min-width: 48px;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--primary-lightest, #ecf6ff);

    @include mixin.small-laptop {
      min-width: 40px;
      width: 40px;
      height: 40px;
    }

    svg {
      width: 24px;
      height: 24px;

      @include mixin.small-laptop {
        width: 20px;
        height: 20px;
      }
    }
  }

  &__data {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;

    @include mixin.small-laptop {
      gap: 8px;
    }
  }

  &__title {
    font-size: 24px;
    font-weight: 600;
    line-height: 1.5;

    @include mixin.small-laptop {
      font-size: 18px;
    }
  }

  &__link {
    span {
      color: var(--primary-darker, #1874c1);
    }
  }
}